'use server'

import { createClient } from '@/utils/supabase/server'
import { redirect } from 'next/navigation'
import { revalidatePath } from 'next/cache'

export async function createAcademy(prevState: any, formData: FormData) {
  const supabase = await createClient()

  // 1. Verificação de Segurança (Apenas Super Admin)
  const { data: userData } = await supabase.from('users').select('role').single()
  if (userData?.role !== 'SUPER_ADMIN') {
    return { error: 'Permissão negada.' }
  }

  const name = formData.get('name') as string
  const slug = formData.get('slug') as string
  const primaryColor = formData.get('primary_color') as string
  const logoFile = formData.get('logo') as File

  // Novos Campos de Endereço V2
  const zipCode = formData.get('zip_code') as string
  const street = formData.get('street') as string
  const number = formData.get('number') as string
  const neighborhood = formData.get('neighborhood') as string
  const city = formData.get('city') as string
  const state = formData.get('state') as string
  const complement = formData.get('complement') as string

  // Tratamento de Lat/Long
  const latStr = formData.get('latitude') as string
  const lngStr = formData.get('longitude') as string
  const latitude = latStr ? parseFloat(latStr) : null
  const longitude = lngStr ? parseFloat(lngStr) : null

  // 2. Validações Básicas
  if (!name || !slug) {
    return { error: 'Nome e Slug são obrigatórios.' }
  }

  // Validar formato do slug (apenas letras minúsculas e hifens)
  const slugRegex = /^[a-z0-9-]+$/
  if (!slugRegex.test(slug)) {
    return { error: 'O Slug deve conter apenas letras minúsculas, números e hífens (sem espaços).' }
  }

  let logoUrl = null

  // 3. Upload do Logo (se houver)
  if (logoFile && logoFile.size > 0) {
    // Gerar nome único para o arquivo
    const fileExt = logoFile.name.split('.').pop()
    const fileName = `${slug}-${Date.now()}.${fileExt}`
    const filePath = `public/${fileName}`

    const { error: uploadError } = await supabase.storage
      .from('academy-logos')
      .upload(filePath, logoFile)

    if (uploadError) {
      console.error('Erro upload:', uploadError)
      return { error: 'Erro ao fazer upload da logo.' }
    }

    // Obter URL pública
    const { data: { publicUrl } } = supabase.storage
      .from('academy-logos')
      .getPublicUrl(filePath)

    logoUrl = publicUrl
  }

  // 4. Inserir no Banco de Dados
  const { error: insertError } = await supabase
    .from('academies')
    .insert({
      name,
      slug,
      primary_color: primaryColor || '#000000',
      logo_url: logoUrl,
      // Novos campos
      zip_code: zipCode || null,
      street: street || null,
      number: number || null,
      neighborhood: neighborhood || null,
      city: city || null,
      state: state || null,
      complement: complement || null,
      latitude: latitude,
      longitude: longitude
    })

  if (insertError) {
    if (insertError.code === '23505') { // Unique violation
      return { error: 'Este Slug já está em uso. Escolha outro.' }
    }
    console.error('Erro insert:', insertError)
    return { error: 'Erro ao cadastrar academia.' }
  }

  // 5. Finalizar
  revalidatePath('/admin/academies')
  redirect('/admin/academies')
}
